.edit-btn, .clear-btn{
    background:none;
    border:none;
    font-size: 30px;
}

.clear-btn{
    color: rgb(102, 10, 51);
}

.edit-btn{
    color: rgb(162, 69, 185);
}

.edit-btn:hover{
    color:rgb(201, 129, 123)
}

.clear-btn:hover{
    color: red;
}
.item{
    list-style: none;
    display: flex;
    padding: 15px;
    font-size: 23px;
    justify-content: space-between;
    margin-right: 30px;
    border-bottom: 2px solid rgb(82, 82, 82);
    color: rgb(52, 52, 52)
}

.ck-btn{
    appearance: none;
    width: 1.25rem;
    height: 1.25rem;
    border: 1px solid rgb(214, 149, 220);
    border-radius: 3px;
    margin-right: 20px;

}

.ck-btn:checked{
    border: 0.5em solid rgb(162, 69, 185);
}

.detail-info, .ck-btn:checked{
    text-decoration: line-through;
}

