@import url('https://fonts.googleapis.com/css2?family=Jua&display=swap');

*{
box-sizing: border-box;
}

body{
  background-color:rgb(221, 202, 242);
  font-family: "Jua", sans-serif;
}

.App{
  display: flex;
  justify-content: center;
  align-items: center;
}

.container{
  width: 80%;
  
  background-color: whitesmoke;
  margin-top: 80px;
  border-radius: 15px;
  border: 5px solid rgb(126, 116, 188);
}

.todo-title{
  font-size: 35px;
  text-align: center;
  color: rgb(255, 255, 255);
  background-color: rgb(173, 140, 190);
  border-radius: 10px 10px 0 0;
  padding: 10px;
  border-bottom: 5px solid rgb(126, 116, 188);
}